<template>
  <div :class="[isQuestion?'forbidScroll':'']">
    <header-bar></header-bar>
    <main>
      <div class="header-img">
        <img :src="drugSet.titleImg" alt="">
      </div>
      <div class="goods-info">
        <div class="goods-name">{{drugSet.commonName}}</div>
        <div class="price-number clearfix">
          <div class="goods-price">
            <i></i>
            {{drugSet.saleGenPrice}}
          </div>
          <div class="number">
            <i class="iconfont iconjianshao" @click="minus"></i>
            <span>{{goodsNum}}</span>
            <i class="iconfont iconadd1" @click="add"></i>
          </div>
        </div>
      </div>
      <div class="goods-parameter">
        <h2></h2>
        <ul class="goods-p-i">
          <li>{{drugSet.itemName}}</li>
          <li>{{drugSet.commonName}}</li>
          <li>{{drugSet.dosege}}{{drugSet.dosegeUnit}}*{{drugSet.divisor}}{{drugSet.doseUnit}}/{{drugSet.saleUnit}}</li>
          <li>{{drugSet.manufactory}}</li>
          <li>{{drugSet.validity}}</li>
          <li>{{drugSet.approvalNumber}}</li>
        </ul>
      </div>
      <div class="goods-parameter">
        <h2></h2>
        <ul class="goods-p-i">
          <img :src="item" alt="" v-for="(item, index) in drugSet.detailsImg" :key="index">
        </ul>
      </div>
      <!---->
      <div class="question" v-if="isQuestion">
        <div class="bg"></div>
        <div class="swiper">
          <div class="test" v-for="(item, index) in testArr" :key="index">
            <div class="one-title h2">{{index+1}}{{item.questionText}}</div>
            <template v-if="item.questionType=='2' || item.questionType=='3'">
              <!---->
              <div class="option" v-for="(ite, inde) in item.list" :key="inde">
                <div class="box" @click="isSelect(index,inde)">
                  <i
                    class="iconfont"
                    :class="[ite.checked?'iconright-f active':'iconcircle2yuanquan']"
                  ></i>
                  <div>{{ite.options}}</div>
                </div>
              </div>
            </template>
            <template v-else>
              <!---->
              <van-field
                type="number"
                class="gap-fill"
                maxlength="50"
                placeholder=""
                v-model="item.note"
                @input="fillInput('number')"
                v-if="item.isNumber==0"
              />
              <van-field
                class="gap-fill"
                maxlength="50"
                placeholder=""
                v-model="item.note"
                @input="fillInput('')"
                v-else
              />
            </template>
          </div>
          <div class="submit" @click="submitQuestinon"></div>
        </div>
      </div>
      <!---->
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2></h2>
        <ul class="goods-p-i">
          <li></li>
          <li>5-[(E)-2 ]-2--1,3-</li>
          <li>C17H18O2 :254.32</li>
          <li>80</li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2></h2>
        <ul class="goods-p-i">
          <li></li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2></h2>
        <ul class="goods-p-i">
          <li></li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2></h2>
        <ul class="goods-p-i">
          <li>10g0.1g(1)</li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2></h2>
        <ul class="goods-p-i">
          <li> 6g10</li>
          <li>1212</li>
          <li></li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2></h2>
        <ul class="goods-p-i">
          <li></li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2></h2>
        <ul class="goods-p-i">
          <li>1</li>
          <li>2</li>
          <li>3</li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2></h2>
        <ul class="goods-p-i">
          <li></li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2></h2>
        <ul class="goods-p-i">
          <li>:</li>
          <li>30mg/kg/6mg/kg/</li>
          <li>:</li>
          <li></li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2></h2>
        <ul class="goods-p-i">
          <li>1818 </li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2></h2>
        <ul class="goods-p-i">
          <li>6565</li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2></h2>
        <ul class="goods-p-i">
          <li></li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2></h2>
        <ul class="goods-p-i">
          <li></li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2></h2>
        <ul class="goods-p-i">
          <li>(20C)</li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2></h2>
        <ul class="goods-p-i">
          <li>YBH01282019</li>
        </ul>
      </div>
      <div class="goods-parameter" v-if="goods.goodsNo == 9999">
        <h2></h2>
        <ul class="goods-p-i">
          <li>2019529</li>
        </ul>
      </div>
      <div class="fill"></div>
    </main>
    <!---->
    <div class="buy-box">
      <div class="btn" @click="submit"></div>
    </div>
  </div>
</template>
<script>
import base from "../assets/js/base.js";
import headerBar from "@/components/header-bar";
import { Dialog } from "vant";
export default {
  name: "goodsInfo",
  data() {
    return {
      goodsNum: 1,
      drugSet: {},
      goods: {},
      testArr: [],
      isQuestion: false
    };
  },
  methods: {
    submit() {
      base
        .postRequest("/buy/insertGoods", {
          goodsNo: sessionStorage.getItem("goodsNo"),
          number: this.goodsNum
        })
        .then(res => {
          if (res.status == 0) {
            sessionStorage.setItem("mallNo", res.data.mallNo);
            setTimeout(() => {
              this.$router.push("affirmOrder");
            }, 100);
          } else {
            base.toast(res.msg);
          }
        });
    },
    minus() {
      if (this.goodsNum > 1) {
        this.goodsNum--;
      } else {
        this.goodsNum = 1;
        base.toast("");
      }
    },
    isSelect(i, j) {
      //
      if (this.testArr[i].questionType == 2) {
        for (let s = 0; s < this.testArr[i].list.length; s++) {
          this.testArr[i].list[s].checked = false;
        }
        this.testArr[i].list[j].checked = true;
      }
      //
      if (this.testArr[i].questionType == 3) {
        this.testArr[i].list[j].checked = !this.testArr[i].list[j].checked;
      }
    },
    //
    fillInput(type, e) {
      var patrn = /^\d+(\.\d+)?$/;
      if (this.fillIndex != null && this.fillIndex2 != null) {
        //
        if (type == "number") {
          if (patrn.test(e.detail.value)) {
            this.testArr[this.fillIndex].note = e.detail.value;
          } else {
            this.toast("");
            this.testArr[this.fillIndex].note = "";
            return;
          }
        } else {
          this.testArr[this.fillIndex].note = e.detail.value;
        }
      }
    },
    //
    submitQuestinon() {
      let that = this;
      let userQuestion = [];
      //
      for (let i = 0; i < this.testArr.length; i++) {
        var obj = {};
        obj.id = null;
        obj.ptNo = null;
        obj.templateNo = this.testArr[i].templateNo;
        obj.questionNo = this.testArr[i].questionNo;
        //
        //
        if (this.testArr[i].questionType == 1) {
          obj.options = this.testArr[i].note;
          if (this.testArr[i].isNull == 1 && !this.testArr[i].note) {
            base.toast("");
            return;
          }
        } else {
          //
          if (this.testArr[i].questionType == 3) {
            let answerArr = this.testArr[i].list.filter(ele => {
              return ele.checked == true;
            });
            if (answerArr.length > 0) {
              let arr = [];
              for (let i = 0; i < answerArr.length; i++) {
                arr.push(answerArr[i].options);
              }

              obj.options = arr.join();
            } else {
              if (this.testArr[i].isNull == 1) {
                base.toast("");
                return;
              }
            }
            //
          } else if (this.testArr[i].questionType == 2) {
            let answerArr = this.testArr[i].list.filter(ele => {
              return ele.checked == true;
            });
            if (answerArr.length > 0) {
              obj.options = answerArr[0].options;
            } else {
              if (this.testArr[i].isNull == 1) {
                base.toast("");
                return;
              }
            }
          }
        }
        userQuestion.push(obj);
      }
      if (
        this.goods.goodsNo == 9999 &&
        (userQuestion[0].options != "" || userQuestion[1].options != "")
      ) {
        Dialog.alert({
          title: "",
          message: ""
        }).then(() => {
          this.$router.push("home");
        });
        return;
      } else {
        this.submitFun(userQuestion);
      }
    },
    submitFun(e) {
      let data = {
        userQuestion: e
      };
      base.postAjax("/buy/insertQuestion", JSON.stringify(data)).then(res => {
        if (res.status == 0) {
          this.isQuestion = false;
          base.openScroll();
        } else {
          base.toast(res.msg);
          this.isQuestion = true;
        }
      });
    },
    add() {
      this.goodsNum++;
    },
    //
    getData() {
      base
        .postRequest("/drugset/selectInfo", {
          drugSetNo: sessionStorage.getItem("drugSetNo")
        })
        .then(res => {
          if (res.status == 0) {
            this.drugSet = res.data.drugSet;
            this.goods = res.data.goods;
          } else {
            base.toast(res.msg);
          }
        });
    },
    //
    getQuestion() {
      base
        .postRequest("/buy/selectQuestion", {
          goodsNo: sessionStorage.getItem("goodsNo")
        })
        .then(res => {
          if (res.status == 0) {
            let arr = [];
            for (let i = 0; i < res.data.questionTemplates.length; i++) {
              let options = [];
              for (let j = 0; j < res.data.questionOptions.length; j++) {
                if (
                  res.data.questionTemplates[i].templateNo ==
                    res.data.questionOptions[j].templateNo &&
                  res.data.questionTemplates[i].questionNo ==
                    res.data.questionOptions[j].questionNo
                ) {
                  res.data.questionOptions[j].checked = false;
                  options.push(res.data.questionOptions[j]);
                  res.data.questionTemplates[i].list = options;
                }
              }
            }
            this.testArr = JSON.parse(
              JSON.stringify(res.data.questionTemplates)
            );
          } else {
            base.toast(res.msg);
          }
        });
    },
    //
    isAnswer() {
      base.forbiddenScroll();
      base
        .postRequest("/buy/isNullQuestion", {
          goodsNo: sessionStorage.getItem("goodsNo")
        })
        .then(res => {
          if (res.status == 0) {
            if (res.data == 0) {
              this.getQuestion();
              this.isQuestion = true;
            } else {
              this.isQuestion = false;
              base.openScroll();
            }
          } else {
            base.toast(res.msg);
          }
        });
    }
  },
  components: {
    "header-bar": headerBar
  },
  created() {
    this.isAnswer();
    this.getData();
  },
  mounted() {
    this.$store.commit("changeActive", "hideFooter");
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/scss/base.scss";
.forbidScroll {
  position: absolute;
  height: 100%;
  width: 100%;
  overflow: hidden;
}
main {
  position: relative;
  top: ptr(44px);
}
.header-img {
  height: ptr(200px);
  padding: ptr(5px) ptr(20px);
  background-color: #fff;
  img {
    width: 100%;
    height: 100%;
  }
}
.goods-info {
  padding: ptr(20px);
  background-color: #fff;
  margin-bottom: ptr(10px);
  .goods-name {
    font-size: ptr(16px);
    margin-bottom: ptr(10px);
  }
  .price-number {
    .goods-price {
      color: red;
      font-size: ptr(16px);
      i {
        font-size: ptr(12px);
        font-style: normal;
      }
    }
    .number {
      float: right;
      font-size: ptr(16px);
      span {
        display: inline-block;
        min-width: ptr(50px);
        text-align: center;
      }
      i {
        color: #666;
        font-size: ptr(20px);
      }
    }
  }
}
.goods-parameter {
  padding: ptr(20px);
  background-color: #fff;
  border-bottom: 1px solid #eee;
  h2 {
    color: #333;
    font-size: ptr(16px);
    margin-bottom: ptr(10px);
  }
  .goods-p-i {
    li {
      color: #666;
      margin-bottom: ptr(8px);
    }
    img {
      height: auto;
      width: 100%;
      display: block;
    }
  }
}
.buy-box {
  position: fixed;
  bottom: ptr(-2px);
  left: 0;
  z-index: 3;
  height: ptr(60px);
  padding: 0 ptr(20px);
  width: 100%;
  box-sizing: border-box;
  background-color: #fff;
  box-shadow: 0 3px 14px 2px rgba(0, 0, 0, 0.12);
  .btn {
    margin-top: ptr(10px);
    font-size: ptr(14px);
    padding: ptr(10px);
    color: #0cb0b5;
    border: 1px solid #0cb0b5;
    border-radius: ptr(4px);
    text-align: center;
  }
}
.question {
  .bg {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 4;
    height: 100%;
    width: 100%;
    background-color: rgba($color: #000000, $alpha: 0.5);
  }
}
.swiper {
  position: absolute;
  background-color: #fff;
  left: 50%;
  top: 0;
  width: ptr(300px);
  height: ptr(500px);
  margin-left: ptr(-150px);
  z-index: 5;
  transition: all 0.2s ease;
  overflow-y: auto;
  border-radius: ptr(4px);
  .test {
    padding: ptr(20px) ptr(20px) 0;
    box-sizing: border-box;
    .h2 {
      font-size: ptr(16px);
    }
    .h3 {
      margin-top: ptr(5px);
      font-size: ptr(15px);
    }
    .one-title {
      color: #333;
    }
    .two-title {
      color: #999;
      margin-left: ptr(23px);
    }

    .three-title {
      color: #555;
      margin: ptr(5px) ptr(20px) 0;
    }

    .option {
      font-size: ptr(15px);
      color: #777;
      margin: 0 ptr(30px);
      .box {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        padding: ptr(10px) 0;
        .iconfont {
          font-size: ptr(20px);
        }
        div {
          flex: 1;
          margin-left: ptr(4px);
        }
      }
      .active {
        color: #0cb0b5;
      }
    }
    .gap-fill {
      margin: ptr(10px) 0 ptr(5px) ptr(30px);
      padding: ptr(5px);
      font-size: ptr(14px);
      border: 1px solid #eee;
      border-radius: ptr(4px);
      outline: none;
      width: ptr(200px) !important;
    }
    .gap-none {
      display: none;
    }
  }
  .submit {
    margin: ptr(20px);
    padding: ptr(8px) 0;
    color: #fff;
    font-size: ptr(14px);
    text-align: center;
    border-radius: ptr(4px);
    background-color: #0cb0b5;
  }
}
.fill {
  height: ptr(60px);
  width: 100%;
}
</style>



